.about-page {
  padding-top: 70px; /* Account for fixed header */
}

.about-header {
  background-color: #007bff;
  color: #fff;
  padding: 5rem 0;
  text-align: center;
}

.about-title {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.about-subtitle {
  font-size: 1.2rem;
  max-width: 800px;
  margin: 0 auto;
}

.about-section {
  padding: 5rem 0;
}

.about-section-alt {
  background-color: #f8f9fa;
}

.about-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3rem;
  align-items: center;
}

.about-grid-reverse {
  grid-template-columns: 1fr 1fr;
  direction: rtl;
}

.about-grid-reverse > * {
  direction: ltr;
}

.about-content h2 {
  font-size: 2rem;
  margin-bottom: 1.5rem;
  color: #333;
}

.about-content p {
  margin-bottom: 1.5rem;
  color: #666;
  line-height: 1.7;
}

.about-image {
  overflow: hidden;
  border-radius: 8px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.about-image img {
  width: 100%;
  height: auto;
  display: block;
  transition: transform 0.3s ease;
}

.about-image img:hover {
  transform: scale(1.03);
}

.text-center {
  text-align: center;
}

.section-subtitle {
  font-size: 1.1rem;
  max-width: 800px;
  margin: 0 auto 3rem auto;
  color: #666;
}

.team-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 2rem;
  margin-top: 3rem;
}

.team-member {
  background-color: #fff;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.team-member:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
}

.team-member-image {
  height: 250px;
  overflow: hidden;
}

.team-member-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.team-member:hover .team-member-image img {
  transform: scale(1.05);
}

.team-member-name {
  font-size: 1.3rem;
  margin: 1.5rem 0 0.5rem;
  padding: 0 1.5rem;
  color: #333;
}

.team-member-role {
  font-size: 0.9rem;
  color: #007bff;
  margin-bottom: 1rem;
  padding: 0 1.5rem;
}

.team-member-bio {
  font-size: 0.9rem;
  color: #666;
  padding: 0 1.5rem 1.5rem;
  line-height: 1.6;
}

.about-cta {
  background: linear-gradient(to right, #007bff, #0099ff);
  color: #fff;
  padding: 5rem 0;
  text-align: center;
}

.cta-content {
  max-width: 800px;
  margin: 0 auto;
}

.cta-content h2 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.cta-content p {
  font-size: 1.2rem;
  margin-bottom: 2rem;
}

@media (max-width: 768px) {
  .about-title {
    font-size: 2rem;
  }
  
  .about-grid {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
  
  .about-grid-reverse {
    grid-template-columns: 1fr;
  }
  
  .about-content {
    order: 1;
  }
  
  .about-image {
    order: 2;
  }
  
  .about-section {
    padding: 3rem 0;
  }
  
  .cta-content h2 {
    font-size: 1.8rem;
  }
} 