/* Hero Section Styles */
.hero-section {
  padding: 160px 0 100px;
  position: relative;
  overflow: hidden;
  background: linear-gradient(180deg, rgba(239, 242, 245, 0.6) 0%, rgba(255, 255, 255, 0.9) 100%);
}

.hero-badge {
  display: inline-block;
  background: linear-gradient(90deg, rgba(37, 99, 235, 0.1) 0%, rgba(59, 130, 246, 0.1) 100%);
  color: #2563eb;
  font-weight: 500;
  padding: 8px 16px;
  border-radius: 50px;
  margin-bottom: 20px;
  font-size: 0.9rem;
}

.hero-title {
  font-size: 3.5rem;
  font-weight: 800;
  line-height: 1.2;
  margin-bottom: 25px;
  color: #1e293b;
}

.text-gradient {
  background: linear-gradient(90deg, #2563eb, #4f46e5);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.hero-text {
  font-size: 1.25rem;
  line-height: 1.7;
  color: #64748b;
  margin-bottom: 30px;
  max-width: 540px;
}

.hero-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  margin-bottom: 25px;
}

.hero-btn-primary {
  padding: 12px 30px;
  border-radius: 50px;
  border: none;
  background: linear-gradient(90deg, #2563eb, #3b82f6);
  color: white;
  font-weight: 500;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px rgba(37, 99, 235, 0.2);
}

.hero-btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 15px rgba(37, 99, 235, 0.3);
  background: linear-gradient(90deg, #1d4ed8, #2563eb);
}

.hero-btn-secondary {
  padding: 12px 30px;
  border-radius: 50px;
  border: 1px solid #2563eb;
  background: transparent;
  color: #2563eb;
  font-weight: 500;
  transition: all 0.3s ease;
}

.hero-btn-secondary:hover {
  background: rgba(37, 99, 235, 0.05);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(37, 99, 235, 0.1);
}

/* Hero Image */
.hero-image-container {
  position: relative;
  text-align: right;
}

.hero-image {
  border-radius: 20px;
  max-width: 100%;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
  transform: perspective(1000px) rotateY(-5deg);
  transition: all 0.5s ease;
}

.hero-image:hover {
  transform: perspective(1000px) rotateY(0deg);
}

.hero-stats-card {
  position: absolute;
  background-color: white;
  border-radius: 16px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
  display: flex;
  align-items: center;
  padding: 15px;
  gap: 15px;
  width: 180px;
  animation: float 3s ease-in-out infinite;
}

.hero-stats-card.card-1 {
  top: 20px;
  left: 0;
  animation-delay: 0s;
}

.hero-stats-card.card-2 {
  bottom: 40px;
  left: 20px;
  animation-delay: 1.5s;
}

@keyframes float {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}

.stats-icon {
  font-size: 2rem;
}

.stats-content h4 {
  font-size: 1.2rem;
  font-weight: 700;
  margin: 0;
  color: #1e293b;
}

.stats-content p {
  font-size: 0.8rem;
  color: #64748b;
  margin: 0;
}

.hero-shape-1 {
  position: absolute;
  top: -100px;
  right: -100px;
  width: 300px;
  height: 300px;
  background: linear-gradient(45deg, rgba(37, 99, 235, 0.1), rgba(79, 70, 229, 0.1));
  border-radius: 50%;
  filter: blur(50px);
  z-index: -1;
  animation: rotate 15s linear infinite;
}

.hero-shape-2 {
  position: absolute;
  bottom: -150px;
  left: -150px;
  width: 400px;
  height: 400px;
  background: linear-gradient(45deg, rgba(236, 72, 153, 0.07), rgba(59, 130, 246, 0.07));
  border-radius: 50%;
  filter: blur(70px);
  z-index: -1;
  animation: rotate 20s linear infinite reverse;
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Features Section */
.features-section {
  padding: 100px 0;
  background: white;
}

.section-title {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 16px;
  color: #1e293b;
}

.section-subtitle {
  font-size: 1.1rem;
  color: #64748b;
  max-width: 700px;
  margin: 0 auto;
}

.feature-card {
  background: white;
  border-radius: 20px;
  padding: 40px 30px;
  height: 100%;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease;
  border: 1px solid rgba(0, 0, 0, 0.03);
}

.feature-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.1);
}

.feature-icon {
  font-size: 3rem;
  margin-bottom: 25px;
  display: inline-block;
}

.feature-title {
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 15px;
  color: #1e293b;
}

.feature-text {
  color: #64748b;
  line-height: 1.7;
}

/* Responsive Styles */
@media (max-width: 992px) {
  .hero-section {
    padding: 140px 0 80px;
    text-align: center;
  }
  
  .hero-text {
    margin-left: auto;
    margin-right: auto;
  }
  
  .hero-buttons {
    justify-content: center;
  }
  
  .hero-image-container {
    margin-top: 50px;
    text-align: center;
  }
  
  .hero-stats-card {
    display: none;
  }
  
  .hero-title {
    font-size: 2.8rem;
  }
}

@media (max-width: 576px) {
  .hero-title {
    font-size: 2.2rem;
  }
  
  .hero-buttons {
    flex-direction: column;
    width: 100%;
  }
  
  .hero-btn-primary, .hero-btn-secondary {
    width: 100%;
  }
  
  .section-title {
    font-size: 2rem;
  }
} 